<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="code_tab">
            <p class="code_tab_item">
              <span :class="{'tab_bj':tab_item==1}" @click="tab_item_fun(1)">{{$t('data.privatekeyimport')}}</span>
              <span :class="{'tab_bj':tab_item==2}" @click="tab_item_fun(2)">{{$t('data.KeyStoreimport')}}</span>
            </p>
            <div class="skey">
              <yd-cell-group title="" style="border:1px solid #ddd">
                  <yd-cell-item style="padding-left: .24rem!important">
                      <yd-textarea slot="right" :placeholder="inpdoia" v-model="content"></yd-textarea>
                  </yd-cell-item>
              </yd-cell-group>

              <yd-cell-group>
                  <yd-cell-item>
                      <span slot="left"></span>
                      <yd-input slot="right" type="password" v-model="codedata.password" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.fillpassword')"></yd-input>
                  </yd-cell-item>
                  <yd-cell-item>
                      <span slot="left"></span>
                      <yd-input slot="right" type="password" v-model="codedata.repassword" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('data.fillduplicatepassword')"></yd-input>
                  </yd-cell-item>
              </yd-cell-group>
              <yd-flexbox class="reda">
                  <div class="chk">
                    <yd-checkbox v-model="codedata.checkbox1" shape="circle" color="#278eed" size="18">&nbsp;</yd-checkbox>
                  </div>
                  <yd-flexbox-item style="font-size: .3rem">{{$t('code.p3')}} <span @click="privacy()">{{$t('code.p4')}}</span></yd-flexbox-item>
              </yd-flexbox>
              <button type="button" class="btn_c" 
                :class="{'btn_cbj':codedata.password && content && codedata.repassword && codedata.checkbox1}" 
                @click="codedata.password && content && codedata.repassword && codedata.checkbox1 && gobeifencode()">{{$t('header.importcode')}}</button>
            </div>
        </div>
    </div>
  
</template>

<script>
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import { bindAndriodToken } from '@/api/android'
  import { getPrivateFun,getKestoreFun } from '../assets/js/Keytoken.js'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codedata:{
        password:'',
        repassword:'',
        check:[0],
        checkbox1:true
      },
      tab_item:1,
      content:'',
      codeaddr:'',
      inpdoia:this.$t('data.Pleaseprivatekey'),
      headerNavData:{
         left:true,
         title:this.$t('header.importcode'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      phoneToken:'',
      parentkey:''
    }
  },
  created(){
    if(this.$route.query.address){
      this.parentkey = this.$route.query.address
    }
  },
  methods:{
    privacy(){
      this.$router.push({path:'/writezjc'})
    },
    tab_item_fun(num){
      this.tab_item = num
      this.content=''
      if(num==1){
        this.inpdoia=this.$t('data.Pleaseprivatekey')
      }else{
        this.inpdoia=this.$t('data.PleasekeyStore') 
      }
    },
    // 导入钱包
    gobeifencode(){
      var that = this
      var password = this.codedata.password
      var repassword = this.codedata.repassword
      var keystore = this.content
      var phoneToken = this.phoneToken
      keystore = keystore.replace(/\s+/g,"")
      if (keystore== "") {this.Toast(this.inpdoia); return}
      if (password == "") {this.Toast(this.$t('data.PleasekeyStore')); return}
      if (repassword == "") {this.Toast(this.$t('data.fillduplicatepassword')); return}
      if (password != repassword) {this.Toast(this.$t('data.Inconsistentpassword')); return}
      that.openloading(this.$t('data.Walleting'))
      // 获取私钥
      if(this.tab_item==2){
        var address = JSON.parse(that.content)
        var publickey = "0x" + address.address
        var parentkey = that.$route.query.address
        getPrivateFun(keystore,password,function(skey){
          if (!skey||skey.length!=66) {
            that.Toast(that.$t('data.passwordincorrect')); 
            that.closeloading(); 
            return
          }
          that.axios({
              url:that.Url + 'Wallet.ashx',
              method:'post',
              params: {req:1,publickey:publickey,nick:'新钱包',parentkey:parentkey}
            }).then(function (res) {
              that.closeloading()
                //钱包绑定手机id
                if(window.phoneToken){
                  bindAndriodToken(publickey,phoneToken)
                }
                var _list = res.data;
                that.setlist(publickey,password,keystore,phoneToken)
                that.Toast(that.$t('data.importsuccess'))
                setTimeout(function(){
                  that.$router.push({path:'/bindphone',query:{org:1,publickey:publickey}})
                },1500)
            }).catch(function(res){
              that.catchErr()
            });
        })
      }else{
        if(keystore.length !=66 && keystore.length !=64 ){that.closeloading(); this.Toast('私钥异常'); return}
        getKestoreFun(keystore, password,function(keystore){
          var publickey = '0x' + keystore.address
          that.axios({
              url:that.Url + 'Wallet.ashx',
              method:'post',
              params: {req:1,publickey:publickey,nick:'新钱包',parentkey:''}
            }).then(function (res) {
              that.closeloading()
                var _list = res.data;
                //钱包绑定手机id
                bindAndriodToken(publickey,phoneToken)
                that.setlist(publickey,password,keystore,phoneToken)
                that.Toast(that.$t('data.importsuccess'))
                setTimeout(function(){
                  that.$router.push({path:'/bindphone',query:{org:1}})
                },1500)
            }).catch(function(res){
              // that.catchErr()
            });
        })
      }
      
      
    },
    //写入本地或者cookie
    setlist(publickey,password,keystore,phoneToken){
      var arry = [];
      var src = Math.ceil(Math.random()*10)
      var url = '../assets/images/head/'+src+'.png'
      var obj = {name:"新钱包",publickey:publickey,url:url,src:src,check:false,keystore:keystore,phoneToken:phoneToken}
      arry.push(obj)
      if (getCookie("codelist")) {
        var codelist =  JSON.parse(getCookie("codelist"))
        for (var i = 0; i < codelist.length; i++) {
          if (publickey == codelist[i].publickey) {
            codelist.splice(i,1)
          }
        }
        var newcodelist = [...codelist, ...arry]
        var jsonStr = JSON.stringify(newcodelist);
        setCookie("codelist",jsonStr)
      }else{
        var jsonStr = JSON.stringify(arry);
        setCookie("codelist",jsonStr)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mat3{
  margin-top: .3rem;
}
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.code_tab_item{
  text-align: center
}
.code_tab_item span{
  display: inline-block;
  padding: 0 .55rem;
  height: .6rem;
  line-height: .6rem;
  font-size: .32rem;
  border-radius: .6rem;
  background: #ddd;
  color: #fff;
  margin: .3rem 10px;
}
.tab_bj{
  background: rgb(44, 148, 237)!important
}
.code_tab_item span:nth-child(2){
  padding: 0 .25rem;
}
/*官方钱包--------------------------------*/
.gfcode{
  padding: .2rem;
}
/*私钥------------------------------------*/
.skey{
  padding: .2rem;
}
.skey .yd-cell{
  border: 1px solid #f4f4f4!important;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4;
  padding-left: 0!important
}
.yd-checkbox-text{
  display: none;
}
.reda{
  padding: 0 0 0 .28rem;
  font-size: .26rem;
  color: #666;
}
.reda span{
  color: #278eed
}
.btn_c{
  margin: .35rem 0 .15rem;
  height: .9rem;
  line-height: .9rem;
  text-align: center;
  width: 100%;
  border: none;
  background: #cdcdcd;
  font-size: .28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_cbj{
  background: #278eed!important;
}
/*观察-------------------------------------*/
.lookcode{
  padding: .2rem;
}
.lookcode_content{
  color: #257ac8;
  font-size: .28rem;
  text-align: justify;
}
.code_input{
  border-bottom: 1px solid #f4f4f4;
  width: 100%;
}
.code_input input{
   border: none;
   height: .8rem;
  line-height: .8rem;
  width: 100%;
  font-size: .3rem;
}
.code_btn{
  border: none;
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  text-align: center;
  background: #278eed;
  color: #fff;
  font-size: .28rem;
  border-radius: 5px;
  margin: .35rem 0
}
.code_btn:active{
  background: #176fbf;
}
.notice{
  padding: .2rem;
  background: #f4f4f4;
  line-height: 1.5;
  font-size: .28rem;
  color: #333;
  text-align: justify;
  border-radius: 5px;
}
.loochk{
  text-align: center;
  color: #278eed;
  font-size: .28rem;
  margin-top: .3rem;
}
.chk{
  font-size: .3rem;
}
</style>



